

.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--bg);
    border-bottom: 1px solid var(--border);
    padding: 10px 15px;
    position: relative;
}

.header h2{
    margin: 0;
}

.menu{
    list-style: none;
    display: flex;
    gap: 30px;
}

.menu li a{
    text-decoration: none;
    color: var(--text);
    transition: .3s;
}

.menu li a:hover{
    color: var(--accent);
}

.activestyle{
    color: var(--accent);
}

img{
    width: 34px;
}

.rights{
    display: flex;
    gap: 10px;
    background-color: var(--bg);
}

.rights button{
    border-radius: 20px;
    border: 1px solid var(--bg);
    background-color: var(--bg);
    cursor: pointer;
    transition: .3s;
    padding: 6px;
}

.rights button:hover{
    border: 1px solid var(--accent);
}

.mu{
    display: none;
    font-size: 22px;
}

.fullscreen-menu{
    position: absolute;
    top: 70px;
    right: 15px;
    width: 230px;
    background: var(--bg);
    border: 1px solid var(--border);
    border-radius: 14px;
    padding: 20px 18px;
    z-index: 999;
    box-shadow: 0 15px 35px rgba(0,0,0,.15);
}

.fullscreen-menu ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 18px;
    padding: 0;
    margin: 0;
}

.fullscreen-menu ul li a{
    text-decoration: none;
    color: var(--text);
    font-size: 16px;
    transition: .3s;
}

.fullscreen-menu ul li a:hover{
    color: var(--accent);
    transform: translateX(6px);
}

.close-btn{
    position: absolute;
    top: 8px;
    right: 10px;
    background: none;
    border: none;
    font-size: 18px;
    cursor: pointer;
    color: var(--text);
}

@media (max-width: 1024px){
    .menu{
        gap: 20px;
    }
}

@media (max-width: 768px){
    #menu{
        display: none;
    }

    .mu{
        display: block;
    }
}
