/* Footer fixed at bottom */
.footer {
  background-color: var(--card);
  color: var(--text);
  text-align: center;
  padding: 1.5rem 1rem;
  border-top: 1px solid var(--border);
  font-size: 0.9rem;
  transition: background-color 0.3s ease, color 0.3s ease;
  position: fixed;   /* ÙŠÙ„Ø²Ù‚Ù‡ ØªØ­Øª Ø§Ù„ØµÙØ­Ø© */
